function countUp(el){
	$(el).each(function () {
		$(this).prop('Counter',0).stop(1,1).animate({
			Counter: $(this).data("number")
		}, {
			duration: 1500,
			easing: 'swing',
			step: function (now) {
				$(this).text(Math.abs(now).toFixed(1));
			}
		});
	});
}
function countUp2(el){
	var _delay = 100;
	$(el).each(function (i) {
		_delay = _delay * 2;
		$(this).prop('Counter',-9).stop(1,1).delay(_delay).animate({
			Counter: $(this).data("number")
		}, {
			duration: 1500,
			easing: 'swing',
			step: function (now) {
				$(this).text(Math.abs(now).toFixed(0));
			}
		});
	});
}

